<template>
    <div class="menu">
        <div class="menu-ul">
            <div class="menu-item" v-for='(menu,index) in menus' :id="'maintab'+index" @click="menuItemClick(index)">
                <i class="iconfont " :class="'icon-'+ menu.path"></i>
                <span class="menu-text">{{menu.titleName}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "v-maintab",
  data() {
    return {
      menus: [
        { path: "webShopHome", titleName: "商城" },
        { path: "combineHome", titleName: "套餐" },
        { path: "me", titleName: "我的账户" }
      ],
      curIndex:-1,
      pathInside:["webShopAll","webShopSpecial","webShopIntegral","webShopAdvise"]
    };
  },
  computed: {},
  methods: {
    menuItemClick(index) {
      if(this.curIndex==index) return;
      let menu=this.menus[index];
      this.$router.replace(menu.path);
    }
  },
  components: {},
  mounted() {
    
  },
  watch: {
    '$route' (to, from) {
        let index=-1;
        for(let i=0;i<this.menus.length;i++){
            let menuItem=this.menus[i];
            if(menuItem.path==to.name){
                index=i;
                break;
            } 
        }
        if(index==-1){
            for(let j=0;j<this.pathInside;j++){
                let insideName=this.pathInside[j];
                if(insideName==to.name){
                    index=j;
                    break;
                }
            }
        }
        if(index==-1) return;
        if(this.curIndex==index) return;
        if(this.curIndex!=-1){
            let lastTab=document.getElementById("maintab"+this.curIndex);
            lastTab.classList.remove("active");
            // alert("main移除了激活状态");
        }
        let curTab=document.getElementById("maintab"+index);
        curTab.classList.add("active");
        this.curIndex=index;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/css/mixin.styl';

.menu {
    position: fixed;
    background-color: #fff;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 20;
    box-shadow: 0 -1px 0px rgba(50, 50, 50, 0.1);
    height: 0.38rem;

    .menu-ul {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        text-align: center;

        .menu-item {
            flex: 1;

            .iconfont {
                color: #acacac;
                font-size: 0.18rem;
            }

            .menu-text {
                margin-top 0.02rem;
                display: block;
                font-size: 0.12rem;
                color: #acacac;
            }
        }

        .active {
            .iconfont {
                color: #a2733d;
            }

            .menu-text {
                color: #a2733d;
            }
        }
    }
}
</style>


