<template>
    <div class="home">
        <v-main-tab ref="mainTab" @touchmove.native.prevent></v-main-tab>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
import vMainTab from "./mainTab.vue";
import { mapState } from "vuex";
import sharejs from "../../common/js/sharejs.js";
import Vue from "vue";
import store from "../../vuex/store";

export default {
  data() {
    return {
      tourls: ""
    };
  },
  components: {
    vMainTab
  },
  beforeRouteEnter(to, from, next) {
    //如果从分享的details跳转过来的
    next(vm => {});
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  created() {
    if (this.$route.name != "home") return;
    let clearId = window.setTimeout(() => {
      this.nextJump();
      window.clearTimeout(clearId);
    }, 100);
  },
  watch: {
    $route(to, from) {
      if (to.name != "home") return;
      let clearId = window.setTimeout(() => {
        this.nextJump();
        window.clearTimeout(clearId);
      }, 100);
    }
  },
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {
    jumpToDetails(goodsId) {
      var pardata = { cardId: goodsId };
      this.$store.commit("UPDATE_CURMEALDATA", pardata);
      this.$router.push({ name: "details" });
    },
    nextJump() {
      var amxl_detailsid = window.getCookie("amxl_detailsid");
      if (amxl_detailsid && parseInt(amxl_detailsid) > 0) {
        window.setCookie("amxl_detailsid", "-1");
        this.jumpToDetails(amxl_detailsid);
        return;
      }

      this.tourls = this.$route.params.tourl;
      if (this.tourls == "me") {
        this.tourls = "";
        // this.$router.replace({ name: "me" });
        this.$refs.mainTab.menuItemClick(2);
      } else {
        // alert("this.$refs.mainTab.curIndex="+this.$refs.mainTab.curIndex);
        this.$refs.mainTab.curIndex=-1;
        this.$refs.mainTab.menuItemClick(0);
        //this.$router.replace({name: "refreshPage",params: { "nextPath": curPath }});
      }
    }
  },
  mounted() {}
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
</style>



